import { useState } from 'react'

import { useExchangeAPI } from '@loopring-web/sdk/hooks/common/useApi'
import { useCustomDCEffect } from '@loopring-web/sdk/hooks/useCustomDCEffect'
import { TradingInterval, GetCandlestickRequest, } from '@loopring-web/sdk/defs/loopring_defs'
import { dumpError400 } from '@loopring-web/sdk/utils/network_tools'
import { fromWEI } from 'utils/swap_calc_utils'

import { ChartUnit } from '@loopring-web/component-lib/static-resource'

import {  ScaleAreaChart, ChartType } from '@loopring-web/chart-lib'
import { Box, Grid } from "@material-ui/core"

import { TGItemData, TGItemJSXInterface, ToggleButtonGroup, } from '@loopring-web/component-lib'
import { WithTranslation } from 'react-i18next'
import { TradeTitle } from '@loopring-web/component-lib/components/block'
import { Candlestick } from '@loopring-web/sdk/defs/exchange_defs'
import { getThousandFormattedNumbers } from '@loopring-web/component-lib/src/static-resource';

const toggleData: TGItemData[] = [
  {
    value: ChartType.Depth,
    key: ChartType.Depth,
    label: 'label' + ChartType.Depth
  },
  {
    value: ChartType.Trend,
    key: ChartType.Trend,
    label: 'label' + ChartType.Trend
  },
]

const BasicInfoPanel = ({ props, swapProps, t, ...rest }: any & WithTranslation) => {

  const tgItemJSXs: TGItemJSXInterface[] = toggleData.map(({ value, label, key }) => {
    return { value, tlabel: t(label), key, JSX: <>{t(label)}</> }
  })

  const { quote, market, tokens } = props

  const [chartType, setChartType] = useState<ChartType>(ChartType.Trend)

  const [change, setChange] = useState(0)

  const [volume, setVolume] = useState('')

  const handleChange = (_e: React.MouseEvent, value: any) => {
    // Settings.setChartType(value)
    console.log('handleChange:', value)
    setChartType(value)
  }

  const exchangeApi = useExchangeAPI()

  useCustomDCEffect(() => {

    if (!exchangeApi || !market || !tokens || !quote) {
      return
    }

    let mounted = true

    const request: GetCandlestickRequest = {
      market,
      interval: TradingInterval.d1,
      limit: 2
    }

    exchangeApi.getCandlestick(request).then((candlesticks) => {

      if (mounted) {
        if (candlesticks?.candlesticks && candlesticks.candlesticks.length > 0) {
          const lastBar = candlesticks.candlesticks[candlesticks.candlesticks.length - 1]
          const lastClose = lastBar.close
          const firstClose = candlesticks.candlesticks[0].close
          const change = firstClose ? ((lastClose - firstClose) / firstClose) : 0

          if (change) {
            setChange(change)
          }

          if (lastBar?.quoteVol) {
            const quoteVol = fromWEI(tokens, quote, lastBar?.quoteVol) as string
            setVolume(quoteVol)
          }
        }
      }

    }).catch((reason) => {
      dumpError400(reason)
    })

    return () => {
      mounted = false
    }

  }, [exchangeApi, market, tokens, quote,])

  const [originData, setOriginData] = useState<any>()

  const [chartTypes, setChartTypes] = useState<ChartType>(ChartType.Trend)

  const tgItemJSXsPriceChart: TGItemJSXInterface[] = Object.keys(ChartUnit).reduce((pre, item) => {
    // @ts-ignore
    const tGItemData: TGItemData = { value: ChartUnit[item], key: ChartUnit[item], label: 'label' + ChartUnit[item] };
    pre.push({ value: tGItemData.value, tlabel: t(tGItemData.label ? tGItemData.label : tGItemData.key), key: tGItemData.key, JSX: <>{t(tGItemData.label ? tGItemData.label : tGItemData.key)}</> })
    return pre
  }, [] as TGItemJSXInterface[])

  useCustomDCEffect(async () => {

    let mounted = true

    if (!exchangeApi || !market) {
      return
    }

    const request: GetCandlestickRequest = {
      market,
      interval: TradingInterval.min15,
      limit: 96
    }

    try {
      const candlesticks = await exchangeApi.getCandlestick(request)
      if (mounted) {
        const originData = candlesticks.candlesticks.map((item: Candlestick) => {
          return {
            timeStamp: item.timeStamp,
            low: item.low,
            high: item.high,
            open: item.open,
            close: item.close,
            volume: item.quoteVol,
          }
        })
        setOriginData(originData)
      }
    }
    catch (reason) {
      dumpError400(reason, 'ChartPanel getCandlestick')
    }

    return () => {
      mounted = false
    }

  }, [exchangeApi, market])

  return (
      <>
        <Box display={'flex'} justifyContent={'space-between'} alignItems={'flex-end'}>
          {swapProps.swapTradeData ? <>
            <TradeTitle {...{
              ...rest, t, ...swapProps, tradeFloat: {
                priceDollar: getThousandFormattedNumbers(change,8),
                percent: '+15%',
                chartUnit: "24h",
                volume:'',
              }
            }}></TradeTitle><ToggleButtonGroup exclusive {...{ ...rest, t, tgItemJSXs, value: chartType }}
                                               handleChange={handleChange} />
          </> : undefined}

        </Box>
        <Grid container>
          <Grid item xs={12} minHeight={256} maxHeight={256}>
            <ScaleAreaChart type={chartType} data={originData ?? []} handleMove={() => { }} />
          </Grid>
          <Grid item xs={12} height={24} display={'flex'} justifyContent={'flex-end'}>
            <ToggleButtonGroup exclusive {...{ ...rest, t, tgItemJSXs: tgItemJSXsPriceChart,
              value: ChartUnit.D1, size: 'small' }}
                               handleChange={() => undefined} />
          </Grid>
        </Grid>
      </>
  )


};

export default BasicInfoPanel
