import { useCallback } from 'react'

import {
    Header as HeaderUI,
    HideOnScroll,
    ModalWalletConnect,
    AccountInfo,
} from '@loopring-web/component-lib'

import { headerRoot } from '@loopring-web/component-lib/src/static-resource'

import { useLocation } from 'react-router-dom'

import { Box, Collapse, Container, Toolbar } from '@material-ui/core'

import { useHeader } from './hook'

import { useDisconnect, } from 'stores/account/hook'
import { ModalPanel } from '@loopring-web/component-lib';
import { useGetExchangeInfo } from 'hooks/exchange/useExchangeAPI'
import { SettingPage } from '../../pages/SettingPage';
import { useModalProps } from './hook'

const Header = ({ ...rest }: any) => {

    const {
      depositProps,
      withdrawProps,
      transferProps,
      resetProps,
    } = useModalProps()

    const location = useLocation()
    
    const {
        headerToolBarData,
        headerMenuData,
        gatewayList,
        open,
        setOpen,
        setShowAccountInfo,
        account,
        accountInfoProps,
        showAccountInfo,
        advanceOpen,
        setAdvanceOpen
    } = useHeader()

    const { exchangeInfo } = useGetExchangeInfo()

    const { disconnect } = useDisconnect()

    const onSwitch = useCallback(() => {
        setOpen(true)
    }, [setOpen])

    return (<>

        <ModalPanel transferProps={transferProps} withDrawProps={withdrawProps} depositProps={depositProps} resetProps={resetProps} />

        <HideOnScroll>
            {process.env.NODE_ENV !== 'production' && JSON.stringify(account?.status)}
            {process.env.NODE_ENV !== 'production' && ' exchangeAddress:' + JSON.stringify(exchangeInfo?.exchangeAddress)}

            <HeaderUI {...rest} headerMenuData={headerMenuData} headerToolBarData={headerToolBarData}
                selected={location.pathname === '/' ? headerRoot : location.pathname}></HeaderUI>

        </HideOnScroll>
        <Toolbar />

        <ModalWalletConnect {...{ ...rest, gatewayList, open, onClose: () => setOpen(false) }} />

        {
            accountInfoProps && <><Collapse in={showAccountInfo}>
                <Container maxWidth="lg">
                    <Box marginTop={2}>
                        <AccountInfo onDisconnect={() => { disconnect(); setShowAccountInfo(false); }} onSwith={onSwitch} {...accountInfoProps}></AccountInfo>
                    </Box>
                </Container>
            </Collapse>
                <SettingPage open={advanceOpen} onClose={e => setAdvanceOpen(false)} />
            </>
        }

    </>)
}

export default Header
