import { TransactionTable } from '@loopring-web/component-lib'
import { WithTranslation, withTranslation } from 'react-i18next'
import { Box, Paper } from '@material-ui/core'
import styled from '@emotion/styled'
import { useGetTxs } from './hooks'

const StylePaper = styled(Box)`
  width: 100%;
  height: 100%;
  flex: 1;
  background-color: ${({ theme }) => theme.colorBase.background().default};
  border-radius: ${({ theme }) => theme.unit}px;
  padding: 20px;

  .title {
    font-family: Gilroy-Medium;
    font-size: ${({ theme }) => theme.unit * 3}px;
    line-height: 19px;
  }

  .tableWrapper {
    margin-top: 20px;
    border: 1px solid ${({ theme }) => theme.colorBase.borderColor};
    border-radius: ${({ theme }) => theme.unit}px;
    padding: 26px;
  }
` as typeof Paper;

const TxPanel = withTranslation('common')((rest:WithTranslation<'common'>) => {

    const { txs } = useGetTxs()

    const txList = txs !== undefined ? txs: []
    

    return (
        <StylePaper>
            <div className="title">Transactions</div>
            <div className="tableWrapper">
                <TransactionTable rawData={txList} {...rest} />
            </div>
        </StylePaper>
        // <StyledTableWrapper>
        //     <TransactionTable rawData={txList} {...{...rest}}/>
        // </StyledTableWrapper>
    )
})

export default TxPanel
